import { Component, Element, Host, Method, Prop, Watch, h } from '@stencil/core';
import { SharedStyles } from '../../utils/shared-styles';
import type { TableColumnInterface } from '../storm-table/interface';
import type { ToolbarItemInterface } from '../storm-toolbar/interface';

@Component({
  tag: 'storm-document-list-detail',
  styleUrl: 'storm-document-list-detail.css',
  shadow: true,
})
export class StormDocumentListDetail {
  @Element() host: HTMLElement;
  @Prop({ mutable: true }) dataSet: object[];
  @Prop({ mutable: true }) columns: TableColumnInterface[];
  @Prop({ mutable: true }) pageNumber: number = 1;
  @Prop({ mutable: true }) pageSize: number = 10;
  @Prop({ mutable: true }) searchText?: string;
  @Prop({ mutable: true }) sortName?: string | number;
  @Prop({ mutable: true }) sortOrder?: 'asc' | 'desc';
  @Prop({ mutable: true, reflect: false }) totalRows: number;
  @Prop() searchable: boolean = false;
  @Prop({ mutable: true }) toolbar: ToolbarItemInterface[];
  @Prop() splitPercent: number = 30;
  @Prop() checkToggle: boolean = true;
  @Prop() clickRowTo: 'select' | 'check' = 'select';
  @Prop() sidePagination: 'razor' | 'stencil' = 'stencil';

  private _table: HTMLStormTableElement;
  private _splitView: HTMLStormVerticalSplitViewElement;
  private _toggle: boolean = false;

  async componentWillLoad() {
    await SharedStyles.applyShadowRootStylesAsync(this.host, 'material');
  }

  render() {
    return (
      <Host>
        <storm-vertical-split-view rightPaneVisible={false} splitPercent={this.splitPercent}>
          <storm-horizontal-split-view slot="left-pane" fixedPane={'top'} fixedSize={'56px'}>
            {/*<div class="storm-toolbar" slot="top-pane">*/}
            {/*  <storm-toolbar dataSet={this.toolbar}></storm-toolbar>*/}
            {/*</div>*/}
            <div slot="top-pane">
              <slot name="top-pane"></slot>
            </div>
            <div slot="bottom-pane">
              <slot name="bottom-pane"></slot>
            </div>
            {/*<storm-table*/}
            {/*  slot="bottom-pane"*/}
            {/*  searchable={this.searchable}*/}
            {/*  pagination={true}*/}
            {/*  escape={false}*/}
            {/*  clickRowTo={this.clickRowTo}*/}
            {/*  classes={{*/}
            {/*    'table-bordered': false,*/}
            {/*    'table-hover': true,*/}
            {/*    'table-striped': true,*/}
            {/*    'table-dark': false,*/}
            {/*    'table-light': true,*/}
            {/*    'table-borderless': true,*/}
            {/*  }}*/}
            {/*  columns={this.columns}*/}
            {/*  dataSet={this.dataSet}*/}
            {/*  pageNumber={this.pageNumber}*/}
            {/*  pageSize={this.pageSize}*/}
            {/*  searchText={this.searchText}*/}
            {/*  sortName={this.sortName}*/}
            {/*  sortOrder={this.sortOrder}*/}
            {/*  totalRows={this.totalRows}*/}
            {/*  sidePagination={this.sidePagination}*/}
            {/*  onSelectDidChange={this.checkToggle ? e => this.handleSelectDidChange(e.detail) : () => {}}*/}
            {/*></storm-table>*/}
          </storm-horizontal-split-view>
          <div slot="right-pane">
            <slot name="right-pane"></slot>
          </div>
          {/*<storm-document-view slot="right-pane"></storm-document-view>*/}
        </storm-vertical-split-view>
      </Host>
    );
  }

  async componentDidRender() {
    this._table = this.host.shadowRoot.querySelector('storm-table');
    this._splitView = this.host.shadowRoot.querySelector('storm-vertical-split-view');
  }

  async handleSelectDidChange(value: number) {
    await this._splitView.viewToggle('right', value > 0);
  }

  @Watch('dataSet')
  async dataSetWatcher() {
    if (this._splitView) await this._splitView.viewToggle('right', false);
  }

  @Method()
  async viewToggle(force: boolean = null) {
    this._toggle = !this._toggle;
    await this._splitView.viewToggle('right', force != null ? force : this._toggle);
  }

  @Method()
  async getChecked(field: string | number): Promise<object[]> {
    return await this._table.getChecked(field);
  }

  @Method()
  async getSelected(): Promise<object[]> {
    return await this._table.getSelected();
  }
}
